.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

body {
  background-color: rgba(0, 0, 0, 0.75);
}

.imageWrapper {
  min-width: 200px;
}
.imageWrapper img {
  object-fit: contain;
  height: 300px;
  width: 100%;
}

.slider {
  position: relative;
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.slider .left {
  position: absolute;
  transition: all 0.3s ease-in;
  left: -100vw;
  top: 0;
}
.slider .active {
  position: relative;
  transition: all 0.3s ease-out;
}
.slider .right {
  position: absolute;
  transition: all 0.3s ease-in;
  top: 0;
  right: 100vw;
}