.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  position: relative;
  margin-top: 4rem;
  padding: 0 40px;
  color: rgba(255, 255, 255, 0.81);
  z-index: 20;
}

.floatAbove {
  margin-top: -12rem !important;
  z-index: 20;
}
@media only screen and (max-width: 676px) {
  .floatAbove {
    margin-top: -2rem !important;
    height: 200px;
  }
}

.heading1 {
  font-family: Montserrat;
  font-weight: 700;
  font-size: 42px;
}

.margin {
  margin-top: 10px;
  margin-bottom: 10px;
}

.theatre {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-position: center;
  background-size: cover;
  overflow: hidden;
}
@media only screen and (max-width: 676px) {
  .theatre {
    height: 36vh;
  }
}
.theatre .actions {
  position: absolute;
  top: 20px;
  right: 50px;
}
.theatre .actions img {
  height: 30px;
  width: 30px;
  margin-left: 10px;
}
@media only screen and (max-width: 676px) {
  .theatre .actions {
    top: 10px;
    right: 10px;
  }
  .theatre .actions img {
    height: 15px;
    width: 15px;
    margin-left: 5px;
  }
}
.theatre .description {
  position: absolute;
  left: 50px;
  top: 200px;
  height: 210px;
  overflow: auto;
  padding-right: 50px;
  font-family: Montserrat;
  z-index: 1;
}
@media only screen and (min-width: 600px) {
  .theatre .description {
    width: 400px;
  }
}
@media only screen and (max-width: 600px) {
  .theatre .description {
    left: 40px;
    top: 50px;
  }
}
.theatre .description h1 {
  font-weight: 400;
  font-size: 36px;
  font-style: normal;
  color: white;
}
@media only screen and (max-width: 600px) {
  .theatre .description h1 {
    font-size: 18px;
  }
}
.theatre .description p {
  font-size: 18px;
  color: #DADADA;
}
@media only screen and (max-width: 600px) {
  .theatre .description p {
    font-size: 10px;
  }
}
.theatre .description .details {
  margin: 30px 0;
  line-height: 0;
  color: white;
}
.theatre .description .details p {
  font-size: 9px;
}
@media only screen and (max-width: 600px) {
  .theatre .description .details {
    display: none;
  }
}
.theatre .description .play {
  display: flex;
  align-items: center;
}
.theatre .description .play img {
  margin-right: 10px;
}
.theatre .description .play span {
  color: white;
}

.trailerBg {
  height: 600px;
  width: 100%;
}

.theatreWrapper {
  margin-top: -70px;
  position: relative;
  height: 600px;
  z-index: 0;
}
@media only screen and (max-width: 676px) {
  .theatreWrapper {
    margin-top: 0px;
    height: 200px;
  }
}

.error {
  background-color: red;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  color: white;
}
.error p {
  all: unset;
  color: white;
}

.success {
  background-color: green;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 70px;
}
.success p {
  all: unset;
  color: white;
}

.backgroundTrailer {
  position: absolute;
  z-index: 0;
  background-color: black;
}

.fadingBackground {
  position: absolute;
  background-image: linear-gradient(to bottom, rgba(20, 20, 20, 0) 0, rgba(20, 20, 20, 0.15) 15%, rgba(20, 20, 20, 0.35) 29%, rgba(20, 20, 20, 0.58) 44%, #323232 68%, #323232 100%);
  background-size: 100% 100%;
  background-position: 0 top;
  background-repeat: repeat-x;
  background-color: transparent;
  width: 100%;
  height: 100%;
  top: auto;
  bottom: -1px;
  opacity: 1;
  z-index: 1;
}