.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  margin-top: 100px;
  margin-left: 30px;
  margin-right: 30px;
  font-family: Montserrat;
}
.container .title {
  font-weight: 700;
  color: rgba(255, 255, 255, 0.5);
}
.container .hero {
  margin-top: 30px;
  width: 100%;
  height: 30vh;
  display: flex;
  flex-direction: column;
  border: solid 1px rgba(255, 255, 255, 0.5);
  justify-content: center;
  align-items: center;
  border-radius: 20px;
}
.container .hero img {
  margin-bottom: 20px;
}

.cards {
  display: grid;
  gap: 10px;
}
@media only screen and (min-width: 768px) {
  .cards {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    gap: 10px;
  }
}

.card {
  background-color: black;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.9);
  padding: 20px;
  color: #fdfdfd;
}