.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.btnCancel {
  color: #EA4C89;
  margin-right: 0.5rem;
}

.newArtistWrapper {
  position: relative;
  background-color: #333333;
  height: 100%;
  margin-top: 3rem;
  padding: 2rem;
}

.newItemFooter {
  margin-bottom: 5rem;
  background-color: #191919;
  padding: 20px;
  color: #FEFEFE;
  border-radius: 0 0 8px 8px;
}

.newContent {
  background-color: #191919;
  padding: 20px;
  color: #FEFEFE;
  border-radius: 8px;
}