.hero {
  position: relative;
  height: 450px;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero .overlay {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero .content {
  width: 63vw;
  z-index: 1;
}
@media screen and (max-width: 768px) {
  .hero .content {
    width: 90vw;
  }
}
.hero .content h1 {
  font-size: 72px;
  font-weight: 900;
}
@media screen and (max-width: 768px) {
  .hero .content h1 {
    font-size: 36px;
  }
}
.hero .content p {
  font-size: 24px;
  font-weight: 100;
  margin-top: 16px;
}
@media screen and (max-width: 768px) {
  .hero .content p {
    font-size: 15px;
  }
}