.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  position: relative;
  min-height: 100%;
  padding: 1rem 2rem 1rem 2rem;
}

.tabsWrapper {
  margin-bottom: 2rem;
}

.bubbleWrapper {
  margin-right: 3rem;
}

.bubble {
  height: 114px;
  width: 114px;
  border-radius: 50%;
  background-color: #FC0135;
  color: #FFFFFF;
  font-size: 3rem;
}

.bubbleTitle {
  color: #FFF;
  font-size: 1.5rem;
}

.titleBorder {
  background-color: #FC0135;
  height: 8px;
  width: 4rem;
}

.title {
  color: #FFF;
  font-size: 4rem;
}

.dataWrapper {
  margin-top: 5rem;
}