.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.formTextAreaWrapper {
  width: 100%;
  font-size: 14px;
  color: #0C1A22;
  height: 120px;
  overflow: hidden;
  margin: 8px 0;
  border-radius: 5px;
  border: solid 1px #F0F0F0;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
}

.isInvalid {
  border-color: red;
}

p.invalidFeedback {
  color: red;
}

.formTextArea {
  background-color: transparent;
  padding: 5px 20px;
  width: 100%;
  height: 100%;
  border: none;
}
.formTextArea:focus {
  outline: none;
}

.textareaGrey {
  background-color: #818181 !important;
  color: white;
}

.textareaGrey::placeholder {
  color: white;
}