.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.genreSelectorContent {
  margin-top: 80px !important;
}

.genreSelectorHeaderWrapper {
  padding: 1rem 2rem;
  width: 60%;
}

.genreSelectorHeader {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 1.6rem;
  color: #FFFFFF;
}

.genreSelectorMenus {
  border-bottom: 8px solid #000000;
  padding-top: 4rem;
  padding-bottom: 2rem;
}

.genreSelectorMenu {
  height: 130px;
  width: 130px;
  background-color: #191919;
  margin-left: 10px;
  margin-bottom: 10px;
  color: #8C8C8C;
}

.genreSelectorMenu:hover {
  cursor: pointer;
}

.genreSelectorMenuActive {
  background-color: #FC0136;
  color: #FFFFFF;
}

.genreSelectorMenuImage {
  height: 75px;
}

.icon {
  max-height: 50px;
  width: 50px;
}

.btn_continue {
  background-color: #FC0136;
  border: none;
  padding: 3px 20px;
  color: white;
  border-radius: 5px;
  box-shadow: 2px 2px 2px #333;
}