.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.avatarInputWrapper {
  height: 100%;
  width: 100%;
  border: 2px solid #8C8C8C;
  position: relative;
}

.avatarInputWrapperImage {
  height: 100%;
  width: 100%;
}

.buttonWrapper {
  position: absolute;
  bottom: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.avatarBtn {
  border: 0;
  background-color: #FC0136;
  border-radius: 3px;
  height: 26px;
  padding: 0 10px;
  color: white;
}
.avatarBtn:focus {
  outline: none;
}

.avatarBtnIcon {
  height: 30px;
  padding: 0 5px;
}

.dragTitle {
  font-size: 24px;
  font-family: Roboto;
  color: #727C7C;
  font-weight: bold;
}