.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 49;
  background: rgba(0, 0, 0, 0.618);
}
.modal .body {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 50;
  width: 81vw;
  height: 81vh;
  background: white;
  padding: 36px;
  border-radius: 15px;
  overflow: scroll;
}