.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.tabMenu {
  color: #000000;
  font-family: Roboto;
  font-size: 12px;
  border: none;
  background-color: transparent;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-right: 3rem;
  text-transform: uppercase;
}
.tabMenu:focus {
  outline: none;
}
@media screen and (max-width: 768px) {
  .tabMenu {
    font-size: 9px;
    padding: 0.5rem 0.5rem;
  }
}

.tabMenuActive {
  font-size: 20px;
  border-bottom: solid 5px #FC0135;
}
@media screen and (max-width: 768px) {
  .tabMenuActive {
    font-size: 14px;
    padding: 0.5rem 0.5rem;
  }
}

.activeTabMenu {
  background-color: #FC0135;
  height: 5px;
  border-radius: 5px;
}

.mobile {
  max-width: 360px;
}