.donut {
  font-size: 16em;
}

.wrapper {
  position: relative;
  width: 1em;
  height: 1em;
  margin: 0px auto;
}
.wrapper .pie {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  clip-path: rect(0, 1em, 1em, 0.5em);
}
.wrapper .pie .halfcircle {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border: 0.1em solid #1abc9c;
  border-radius: 50%;
  clip-path: rect(0, 0.5em, 1em, 0);
}
.wrapper .pie .right {
  transform: rotate(0deg);
}
.wrapper .pie .label {
  position: absolute;
  top: 0.52em;
  right: 0.4em;
  bottom: 0.4em;
  left: 0.4em;
  display: block;
  background: none;
  border-radius: 50%;
  color: #7F8C8D;
  font-size: 0.25em;
  line-height: 2.6em;
  text-align: center;
  cursor: default;
  z-index: 2;
}
.wrapper .pie .label .unit {
  padding-bottom: 12px;
  color: #BDC3C7;
  font-size: 0.45em;
  vertical-align: super;
}
.wrapper .shadow {
  width: 100%;
  height: 100%;
  border: solid 0.1em #BDC3C7;
  border-radius: 50%;
}