.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.header {
  background-color: black;
  padding: 15px 30px;
}
@media only screen and (max-width: 676px) {
  .header {
    padding: 15px;
  }
}

.body {
  position: relative;
  display: block;
  overflow-x: hidden;
}

.wrapper {
  position: relative;
  display: block;
}

.left {
  position: absolute;
  left: -45px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.right {
  position: absolute;
  right: -45px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}