.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.shareDescriptionWrapper {
  flex: 2;
  padding: 1rem;
}

.shareSocialsWrapper {
  flex: 1;
  padding: 1rem;
}

.shareInput {
  padding: 0 1rem;
  background-color: #818181;
  color: white;
}
.shareInput:focus {
  outline: none;
  padding: 0 1rem;
}

.shareRegionText {
  font-size: 16px;
  font-weight: bold;
}

.textInputContainer {
  width: 100%;
  height: 40px;
  border-radius: 20px;
  border: 1px solid #C4C4C4;
  margin: 8px 0;
  overflow: hidden;
}