.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

body {
  background-color: rgba(0, 0, 0, 0.75);
}

.list {
  overflow-x: auto;
}
.list .itemWrapper {
  position: relative;
  margin-right: 10px;
}
.list .itemWrapper:before {
  content: "";
  position: absolute;
  height: 10px;
  background: red;
}
.list .itemWrapper .thumb {
  position: absolute;
  height: 45px;
  width: 45px;
  object-fit: cover;
  border-radius: 100%;
  border: solid 1px #ffffff;
  top: 5px;
  left: 5px;
}
.list .itemWrapper .story {
  height: 200px;
  width: 120px;
  object-fit: cover;
  border-radius: 5px;
}
.list .itemWrapper .name {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  border-radius: 0 0 5px 5px;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
}
.list .itemWrapper .name .createButton {
  position: absolute;
  background: #8103FC;
  border-radius: 9999px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  margin-top: -45px;
  border: solid 1px #fff;
}
.list .itemWrapper .name .createButton img {
  height: 14px;
}
.list .itemWrapper .name span {
  color: #ffffff;
  font-size: 12px;
}