.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

body {
  background-color: rgba(0, 0, 0, 0.75);
}

.wrapper {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 10px;
}

.input {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid grey;
  border-radius: 9999px;
  padding: 10px 10px;
  color: grey;
}
.input input {
  background: transparent;
  border: none;
}
.input input:focus {
  outline: none;
  caret-color: grey;
}
.input:focus-within {
  border-color: rgba(252, 1, 53, 0.7);
}

.createPostHeader {
  border-bottom: solid 1px grey;
}

.inputBase {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid grey;
  width: 100%;
  border-radius: 10px;
  padding: 10px 10px;
  color: grey;
}
.inputBase:focus {
  outline: none;
  caret-color: grey;
  border-color: rgba(252, 1, 53, 0.7);
}

.pills > div {
  margin-bottom: 10px;
}

.iconPill {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 9999px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
}
.iconPill:hover {
  background: #8103FC;
}
.iconPill.active {
  background: #8103FC;
}