.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  margin-top: 70px;
  margin-left: 50px;
  margin-right: 50px;
}

.cardWrapper div:first-child {
  margin-bottom: 20px;
}
@media only screen and (min-width: 768px) {
  .cardWrapper {
    display: flex;
    margin-top: 50px;
  }
  .cardWrapper div:first-child {
    margin-right: 10px;
  }
}

.card {
  position: relative;
  flex: 1;
  background-image: linear-gradient(#FC0135, #8103FC);
  height: 50vh;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.8);
  color: white;
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
}
.card h1 {
  font-family: Montserrat;
  font-weight: 700;
  font-size: 36px;
  margin-top: 90px;
}
@media only screen and (min-width: 768px) {
  .card h1 {
    margin-top: 0;
    margin-bottom: 81px;
  }
}
.card p {
  font-family: Montserrat;
  font-size: 18px;
  color: white;
}
.card .spacer {
  flex: 1;
}
.card div.price {
  position: absolute;
  top: -10px;
  right: 20px;
  background-color: white;
  color: #000000;
  width: 92.7px;
  height: 120px;
  box-shadow: -5px -5px 3px rgba(0, 0, 0, 0.1);
  border-radius: 0 0 9999px 9999px;
  padding: 10px;
  text-align: center;
}
.card div.price h3 {
  font-size: 12px;
}
.card div.price h3 span {
  font-size: 10px;
}
.card button {
  all: unset;
  border: 1px solid white;
  padding: 20px 10px;
  min-width: 90px;
  border-radius: 9999px;
  text-align: center;
}

.title {
  font-family: Montserrat;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.5);
}

.panelDanger {
  background-color: #ff4444;
  padding: 20px;
  color: #0b0b0b;
}
.panelDanger p {
  color: #0b0b0b;
}

.subscribed {
  background: green;
  padding: 10px 20px;
  border-radius: 10px;
}