.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.preview {
  height: 150px;
  width: 200px;
  margin-right: 1.6rem;
  margin-bottom: 1rem;
}

.previewAvatar {
  user-select: none;
  -webkit-user-drag: none;
  height: 100%;
  width: 100%;
  margin-bottom: 10px;
  background-color: #C1C1C1;
}

.previewRounded {
  border-radius: 100%;
  height: 140px;
  width: 140px;
  background-color: #C1C1C1;
  overflow: hidden;
}

.img {
  height: 100%;
  width: 100%;
}