.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.header {
  background: black;
}

.section {
  background: black;
}

.faqIcon {
  width: 10px;
  transition: all 0.3s ease-in-out;
  transform: rotate(45deg);
}

.faqIcon.active {
  transition: all 0.3s ease-in-out;
  transform: rotate(0deg);
}

.description {
  height: 0;
  overflow: hidden;
  transition: height 0.3s ease-in-out;
}

.description.active {
  height: auto;
  margin-top: 5px;
  padding: 10px 20px;
  transition: height 0.3s ease-in-out;
}