.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.playerWrapper {
  height: 60px;
  background-color: #8103FC;
  padding: 5px 2rem;
  position: relative;
}

.playerNameWrapper {
  color: #FFFFFF;
}

.playerAvatar {
  height: 40px;
  width: 40px;
  border-radius: 20px;
}

.playerBtn {
  width: 30px;
  background-color: transparent;
  border: none;
}
.playerBtn:focus {
  outline: none;
}

.playerVolumeBtn {
  height: 100%;
  position: relative;
  top: -5px;
}
.playerVolumeBtn img {
  height: 15px;
}

.playerSliderWrapper {
  flex-grow: 1;
  height: 100%;
  padding-top: 10px;
}

.playerVolumeWrapper {
  width: 100px;
  height: 100%;
}

.pauseBtnWrapper {
  display: flex;
  height: 20px;
  width: 20px;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  border-radius: 10px;
}

.playerTime {
  padding: 5px 10px;
  color: #FFFFFF;
}

.size17 {
  width: 17px;
}

.playList {
  position: absolute;
  display: block;
  bottom: 65px;
  right: 5px;
  visibility: visible;
  opacity: 1;
  transition: all 0.3s;
}

.playListHidden {
  visibility: hidden;
  opacity: 0;
}

.cursor {
  cursor: pointer;
}