.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.homeContent {
  padding: 0 2.5rem 5rem;
  color: #FFFFFF !important;
}

.homeTabsWrapper {
  margin-top: 2rem;
  margin-bottom: 2rem;
}
@media screen and (max-width: 576px) {
  .homeTabsWrapper {
    text-align: center;
  }
}

.homeHeading {
  font-family: Montserrat;
  font-weight: 700;
  font-size: 24px;
  color: #FC0135;
}

.heading {
  font-size: 2.2rem;
  color: #FFFFFF;
  font-family: "Montserrat";
}