.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.textInputContainer {
  width: 100%;
  height: 40px;
  border-radius: 10px;
  background-color: #F4F4F4;
  border: 1px solid rgba(0, 0, 0, 0);
  margin: 8px 0;
  overflow: hidden;
}

.isInvalid {
  border-color: rgba(255, 0, 0, 0);
}

p.invalidFeedback {
  color: red;
}

.textInputIcon {
  padding: 0 1rem;
  height: 20px;
}

.GreyStyleWrapper {
  background-color: #818181 !important;
}

.GreyStyleWrapper input:-internal-autofill-selected {
  color: -internal-light-dark(#818181, white) !important;
}

.GreyStyleWrapper input:-webkit-autofill {
  color: -internal-light-dark(#818181, white) !important;
}

.GreyStyle {
  background-color: #818181 !important;
  color: white;
  -webkit-box-shadow: none !important;
}

.textInputWrapper {
  padding: 10px 15px;
  width: 100%;
  border: none;
  -webkit-box-shadow: 0 0 0px 1000px #F4F4F4 inset;
}
.textInputWrapper:focus {
  outline: none;
}
.textInputWrapper::placeholder {
  color: black;
  opacity: 0.618;
}
.textInputWrapper.GreyStyle::placeholder {
  color: white;
  font-size: 10px;
  opacity: 0.75;
}