.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.appHeaderWrapper {
  position: sticky;
  top: 0;
  height: 70px;
  padding: 0.5rem 2.5rem;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10000;
}
@media screen and (max-width: 576px) {
  .appHeaderWrapper {
    background-color: black;
    padding: 0.5rem 1rem;
  }
}

.mobile {
  width: 100%;
}

.searchInactive {
  background-color: transparent;
}

.appHeaderAvatar {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}

.appHeaderName {
  color: #FFFFFF;
  padding: 0 1rem;
  background-color: black;
  padding: 2px;
  border-radius: 21px;
  margin-left: 10px;
}
.appHeaderName span {
  padding: 0 0.5rem;
  width: 14ch;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.appHeaderInput {
  background-color: #000000;
  border-color: #000000;
}
.appHeaderInput input {
  color: #FFFFFF;
}
@media screen and (max-width: 576px) {
  .appHeaderInput {
    visibility: hidden;
  }
}

.mobile_logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}
@media screen and (min-width: 576px) {
  .mobile_logo {
    display: none;
  }
}

.mobile_logo_plus {
  display: none;
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}
@media screen and (min-width: 576px) {
  .mobile_logo_plus {
    display: block;
  }
}

.logo_geneneral {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.optSecondary {
  color: #FC0135;
}
.optSecondary:focus, .optSecondary:active, .optSecondary:hover {
  color: #FC0135;
}

.menuWrapper {
  margin-top: 1rem;
}