.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  position: relative;
  min-height: 100%;
  margin-top: 0 !important;
  padding: 2rem 2rem 10rem 2rem;
}

.commentsWrapper {
  flex: 1;
}

.descriptionWrapper {
  padding: 2rem 0 1rem;
}

.description {
  border-bottom: solid 1px grey;
}

.likeWrapper {
  cursor: pointer;
}
.likeWrapper span {
  font-size: 1.2em;
  font-weight: 900;
}
@media (max-width: 992px) {
  .likeWrapper {
    flex-direction: column;
    align-items: center;
  }
  .likeWrapper span {
    font-size: 0.8em;
    font-weight: 500;
  }
}

@media (max-width: 992px) {
  .stickyPlayer {
    position: fixed;
    top: 70px;
    left: 0;
    width: 100%;
    z-index: 1;
  }
}

.commentsToggle {
  border-bottom: solid 1px grey;
}
.commentsToggle p {
  margin: 0;
  margin-bottom: 5px;
}