.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.uploadTrailer {
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease-in;
  background-color: #FC0135;
}
.uploadTrailer p {
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.75);
}
.uploadTrailer img {
  height: 30px;
}
.uploadTrailer img, .uploadTrailer p {
  z-index: 1;
}
.uploadTrailer:hover {
  transition: all 0.3s ease-out;
}
@media (max-width: 992px) {
  .uploadTrailer {
    min-height: 150px;
  }
  .uploadTrailer img {
    height: 20px;
  }
  .uploadTrailer p {
    font-size: 12px;
  }
}
.uploadTrailer.active {
  background-color: black;
}
.uploadTrailer:hover {
  background-color: rgba(252, 1, 53, 0.8);
}
.uploadTrailer.active:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.uploadTrailer video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.uploadCover {
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease-in;
  background-color: #8103FC;
}
.uploadCover p {
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.75);
}
.uploadCover img {
  height: 30px;
}
.uploadCover img, .uploadCover p {
  z-index: 1;
}
.uploadCover:hover {
  transition: all 0.3s ease-out;
}
@media (max-width: 992px) {
  .uploadCover {
    min-height: 150px;
  }
  .uploadCover img {
    height: 20px;
  }
  .uploadCover p {
    font-size: 12px;
  }
}
.uploadCover:hover {
  background-color: rgba(129, 3, 252, 0.8);
}