.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.panelContainer {
  min-height: 100vh;
  padding-top: 100px;
  padding-bottom: 5rem;
}

.inputFormWrapper {
  padding: 1rem;
  border: 1px solid #C1C1C1;
  margin: 1rem 0;
}

.inputContainer {
  background-color: #FFFFFF;
  padding: 1rem;
}

.title {
  font-size: 1.2em;
  color: #000000;
}

.paneTitle {
  font-size: 1.8rem;
  color: #8C8C8C;
  font-weight: bold;
}

.cover {
  width: 100%;
  aspect-ratio: 27/40;
  margin-top: 1.5rem;
}

.movieAspectRatio {
  aspect-ratio: 27/40;
}

.videoAspectRatio {
  aspect-ratio: 27/40;
}

.spinnerLg {
  width: 2rem;
  height: 2rem;
}

.notification {
  position: fixed;
  width: 100%;
  background: green;
  z-index: 10000000000000000;
  padding: 20px 40px;
  bottom: 0;
  left: 0;
  font-weight: 400;
  color: white;
  transition: all 0.3s ease-in-out;
}
.notification .active {
  bottom: 0;
  transition: all 0.3s ease-in-out;
}