.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.loader {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.theatre {
  position: relative;
  width: 100%;
  height: 100%;
  background: black;
  overflow: hidden;
}
.theatre video {
  width: 100vw;
}

.back {
  position: fixed;
  display: inline-block;
  top: 20px;
  left: 20px;
  z-index: 999;
  color: white;
}

.controls {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0 20px;
  color: white;
  background: white;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 6%, black 100%);
}
.controls .range {
  display: flex;
  align-items: center;
}
.controls .range .sliderWrapper {
  position: relative;
  flex: 1;
  height: 18px;
}
.controls .range .sliderWrapper .bottom {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
  z-index: 1;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.45);
}
.controls .range .sliderWrapper .buffered {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 2;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.6);
  transition: all 0.2s ease-in-out;
}
.controls .range .sliderWrapper .progress {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 3;
  height: 3px;
  background-color: #8103FC;
}
.controls .range .sliderWrapper .slider {
  appearance: none;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
  z-index: 4;
  background-color: transparent;
  height: 3px;
  outline: none;
  transition: all 0.3s ease-in;
}
.controls .range .sliderWrapper .slider::-webkit-slider-thumb {
  appearance: none;
  width: 18px;
  height: 18px;
  background-color: #8103FC;
  cursor: pointer;
  border-radius: 25px;
}
.controls .range span {
  padding-left: 10px;
  color: white;
}
.controls .actions {
  display: flex;
  padding: 15px 0;
  align-items: center;
}
.controls .actions .main {
  display: flex;
  flex: 1;
  align-items: center;
}
.controls .actions .quality {
  position: relative;
}
.controls .actions .quality .menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  bottom: 75px;
  right: 0;
  background-color: rgba(31, 32, 35, 0.6);
}
.controls .actions .quality .menu div {
  padding: 15px 20px;
  width: 180px;
  cursor: pointer;
}
.controls .actions .quality .menu div:hover {
  background-color: rgba(255, 255, 255, 0.3) !important;
}
.controls .actions .quality .menu div.active {
  background-color: rgba(255, 255, 255, 0.6);
}
.controls .volume {
  display: flex;
  margin-left: 10px;
  align-items: center;
}
.controls .volume .sliderWrapper {
  position: relative;
  display: none;
  margin-left: 10px;
  flex: 1;
  height: 18px;
  width: 100px;
}
.controls .volume .sliderWrapper .bottom {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
  z-index: 1;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.45);
}
.controls .volume .sliderWrapper .progress {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 3;
  height: 3px;
  background-color: #8103FC;
}
.controls .volume .sliderWrapper .slider {
  appearance: none;
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
  z-index: 4;
  background-color: transparent;
  height: 3px;
  outline: none;
  transition: all 0.3s ease-in;
}
.controls .volume .sliderWrapper .slider::-webkit-slider-thumb {
  appearance: none;
  width: 18px;
  height: 18px;
  background-color: #8103FC;
  cursor: pointer;
  border-radius: 25px;
}
.controls .volume:hover .sliderWrapper {
  display: block;
}