.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  position: relative;
  min-height: 100%;
  margin-top: 5rem !important;
  padding: 2rem 2rem 10rem 2rem;
}

.title {
  color: #8C8C8C;
  font-size: 20px;
  font-family: Roboto;
  margin: 2rem 0;
}

.footerWrapper {
  margin: 4rem 0;
}

.card {
  background-color: rgba(0, 0, 0, 0.75) !important;
}