.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.listNumWrapper {
  color: #FC0135;
  font-size: 1.5rem;
  border: 2px solid #FC0135;
  border-radius: 50%;
  width: 40px;
  min-width: 40px;
  height: 40px;
  margin-right: 2rem;
}

.listHeader {
  color: #FC0135;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.listContentWrapper {
  margin-right: 2rem;
}

.listDescription {
  color: #FFFFFF;
  font-size: 15px;
}