.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.dropdownWrapperBtn {
  background-color: transparent;
  border: none;
}
.dropdownWrapperBtn:focus {
  outline: none;
}

.dropdownBubbleMenu {
  text-align: left;
  padding: 1rem 1.5rem;
  display: flex;
  flex-direction: column;
  background-color: #323232;
  border-radius: 10px;
  /*
  &::before, &::after {
    content: ' ';
    border-style: solid;
    border-width: 0 .5em .8em .5em;
    border-color: transparent;
    position: absolute;
    right: 4em;
  }
  */
  /*
  &::after {
    top: 3.5em;
    border-bottom-color: $white;
  }
  */
}

.dropdownOption {
  height: 40px;
  font-family: Helvetica;
  color: #FFFFFF;
  text-decoration: transparent;
}
.dropdownOption:focus, .dropdownOption:active, .dropdownOption:hover {
  color: #FFFFFF;
  cursor: pointer;
}

.dropdownElementMenu {
  width: 50rem;
}

.content {
  background-color: #FFFFFF;
  position: absolute;
  right: 10px;
  box-shadow: 0px 1px 3px #C4C4C4;
  border-radius: 10px;
}