.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

body {
  background-color: rgba(0, 0, 0, 0.75);
}

.wrapper {
  background: rgba(0, 0, 0, 0.9);
  position: -webkit-sticky;
  /* Safari */
  position: sticky;
  top: 0;
}

.input {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid grey;
  border-radius: 9999px;
  padding: 10px 10px;
  color: grey;
}
.input input {
  background: transparent;
  border: none;
}
.input input:focus {
  outline: none;
  caret-color: grey;
}
.input:focus-within {
  border-color: rgba(252, 1, 53, 0.7);
}

.profile {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid grey;
  border-radius: 9999px;
}
.profile img {
  border-radius: 100%;
  height: 30px;
  width: 30px;
}

.iconButton {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid grey;
  border-radius: 9999px;
  height: 35px;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.iconButtonBase {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid grey;
  border-radius: 9999px;
  cursor: pointer;
}

.secondaryWrapper {
  position: relative;
  background: black;
}
.secondaryWrapper:after {
  content: "";
  position: absolute;
  width: 96%;
  height: 2px;
  background: #00d4ff;
  background: linear-gradient(90deg, #FC0135 0%, #8103FC 46%, #FC0135 100%);
  bottom: 0;
  left: 2%;
  z-index: 1;
}

.secondaryMenu span {
  margin-right: 20px;
  padding: 10px 10px;
  font-size: 18px;
  cursor: pointer;
}
.secondaryMenu span.active {
  border-bottom: 5px solid #FC0135;
}

.socialButton {
  margin-left: 10px;
  margin-bottom: 20px;
  padding: 0 20px;
  cursor: pointer;
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 900;
}
.socialButton span {
  font-weight: 400;
  -webkit-text-fill-color: grey;
}