.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.searchResultItem {
  border-bottom: 1px solid rgba(195, 195, 195, 0.3);
  padding: 1rem 0;
  width: 360px;
  margin-bottom: 10px;
}
.searchResultItem.is-last {
  border: none;
}

.searchResultImage {
  height: 75px;
  width: 75px;
  margin-right: 15px;
}

.searchResultWrapper {
  position: absolute;
  margin-top: 50px;
  width: calc(100vw - 220px);
  z-index: 9999;
  background-color: #1A394B;
  height: calc(100vh - 120px);
  padding: 40px;
  overflow: auto;
}
.searchResultWrapper p {
  color: #FFFFFF;
}

.searchWrapperMobile {
  width: 100%;
}

.heading {
  font-weight: bold;
  font-size: 16px;
  color: #FFFFFF;
}

.isNotActive {
  background-color: #727C7C;
  border-color: #727C7C;
}