.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.customBtn {
  background-color: transparent;
  border: 0;
}
.customBtn img {
  min-height: 15px;
}
.customBtn:focus {
  outline: none;
}

.mkBtn {
  height: 40px;
  border-radius: 42px;
  min-width: 140px;
  border: 0;
  background-color: #FC0135;
  border: 1px solid #FC0135;
  color: #FFFFFF;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 0rem 1rem;
}
.mkBtn:focus {
  outline: none;
}
@media screen and (max-width: 768px) {
  .mkBtn {
    min-width: unset;
    padding: 0rem 0.3rem;
  }
}

.mkBtnSquare {
  border-radius: 5px;
}

.mkBtnStretch {
  width: 100%;
}

.noWidth {
  width: auto;
}

.noBorder {
  border: none;
  min-width: 0;
}

.mkBtnTransparent {
  background-color: transparent;
}

.mkBtnRed {
  background-color: #EA4235;
}

.mkBtnBorderWhite {
  border: 1px solid #FFFFFF;
}

.mkBtnPrimary {
  color: #FC0135;
}

.mkBtnSecondary {
  background-color: #367CA6;
  border: none;
}

.mkBtnSecondaryTransparent {
  color: #367CA6;
}

.borderSecondary {
  color: #FFFFFF;
  border-color: #FFFFFF;
}

.borderPrimary {
  color: #FC0135;
  border-color: #FC0135;
}

.alignLeft {
  text-align: left;
}

.noWrap {
  white-space: nowrap;
}