.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.sideMenu {
  position: relative;
  background-color: black;
  height: 100%;
  width: 100%;
  padding: 3rem 1rem 1rem 0rem;
  overflow-y: auto;
  overflow-x: hidden;
}

.sideMenu.sideMenuHidden {
  padding: 0;
}

.sideMenu::-webkit-scrollbar {
  width: 15px;
}

.sideMenu::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

.sideMenu::-webkit-scrollbar-thumb {
  background: #333;
  border: 1px solid black;
  border-radius: 20px;
}

.sideMenu::-webkit-scrollbar-thumb:hover {
  background: #666;
}

.sideMenu::-webkit-scrollbar-corner {
  background: transparent !important;
}

.headerTitle {
  font-family: Montserrat;
  font-weight: bold;
  font-size: 18px;
  color: #FC0135;
}

.sideMenusWrapper {
  position: relative;
  margin-top: 4rem;
  min-height: 100vh;
}

.sideMenuItem {
  padding: 5px 1rem;
  margin-left: 10px;
  color: #8C8C8C;
  text-decoration: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.sideMenuItem.active, .sideMenuItem:hover {
  background-color: rgba(252, 252, 252, 0.1);
  color: #FC0135;
  border-radius: 7px;
}

.sidemenuItemTitleWrapper {
  background-color: #000000;
}

.sideMenuItemTitle {
  color: white;
  background-color: #8103FC;
  border-radius: 7px;
}

.sideMenuItemIcon {
  padding: 0 1rem;
  height: 15px;
  width: 45px;
}

.sideMenuSubtitle {
  font-family: Helvetica;
  font-weight: 700;
  font-size: 16px;
  color: #FFFFFF;
  margin-left: 2rem;
}

.artistMenus {
  margin-top: 4rem;
}

.logoWrapper {
  background-color: black;
  width: 220px;
  height: 5rem;
  top: 0;
}

.sidemenu_logo {
  width: 100px;
}

.socialButton {
  margin-left: 10px;
  margin-bottom: 20px;
  padding: 0 20px;
  cursor: pointer;
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 900;
}
.socialButton span {
  font-weight: 400;
  -webkit-text-fill-color: grey;
}