.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.artistViewContainer {
  margin-top: 2.5rem;
  padding-bottom: 8em;
}
.artistViewContainer p {
  color: #FFFFFF !important;
}

.artistCoverWrapper {
  height: 350px;
  width: 100%;
}

.homeTabsWrapper {
  margin-top: 0rem;
  margin-bottom: 2rem;
}

.artistHeaderWrapper {
  position: relative;
  top: -70px;
  padding: 1rem 4rem;
  background-image: linear-gradient(to top, #323232, transparent);
}

.artistHeaderInfopane {
  flex: 2;
}

.artistHeaderActionpane {
  flex: 1;
  padding-top: 12px;
  margin-left: 9px;
}

.artistTitle {
  font-family: Roboto;
  font-weight: 700;
  font-size: 25px;
  color: #FFFFFF;
  text-shadow: 0px 0px 2px #000000;
}

.artistSubtitle {
  font-family: Roboto;
  font-weight: 700;
  font-size: 18px;
  border-bottom: 3px solid #EA4C89;
  color: #FFFFFF;
  text-shadow: 0px 0px #000000;
  width: 50px;
}

.artistActionIcon {
  width: 20px;
  color: red;
}