.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 10px;
  padding: 20px;
}
.wrapper h3 {
  color: white;
  font-size: 24px;
  margin-bottom: 20px;
}

.dragIcon {
  height: 15px;
  width: 15px;
}

.settingsIcon {
  height: 25px;
  width: 25px;
  color: white;
}

.contentWrapper {
  flex: 1;
  padding: 0 2rem;
}

.deleteBtn {
  background-color: #FF002F;
  color: #FFFFFF;
  border: none;
}

.closeBtn {
  margin: 0 10px;
}