.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  width: 960px;
  margin: 0 auto;
}
@media screen and (max-width: 768px) {
  .wrapper {
    width: 100vw;
  }
}
.wrapper .trailer {
  position: relative;
  display: block;
  background-color: #323232;
  width: 960px;
  margin: 0 auto;
  overflow: hidden;
  height: 540px;
}
@media screen and (max-width: 768px) {
  .wrapper .trailer {
    width: 100vw;
    height: auto;
  }
}
.wrapper .trailer video {
  width: 100%;
}
.wrapper .trailer .close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .wrapper .trailer .close {
    left: 20px;
  }
}
.wrapper .trailer .close img {
  height: 50px;
  width: 50px;
}
@media screen and (max-width: 768px) {
  .wrapper .trailer .close img {
    width: 30px;
    height: 30px;
  }
}
.wrapper .trailer .shade {
  position: absolute;
  display: block;
  z-index: 2;
  height: 200px;
  width: 100%;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to bottom, transparent 0%, #323232 100%);
}
.wrapper .trailer .description {
  position: absolute;
  z-index: 3;
  bottom: 20px;
  left: 20px;
  color: white;
}
.wrapper .content {
  display: flex;
  margin: 20px;
}
@media screen and (max-width: 768px) {
  .wrapper .content {
    flex-direction: column;
  }
}
.wrapper .content p {
  color: white;
}
.wrapper .content .description {
  flex-grow: 1;
  margin-right: 20px;
}
.wrapper .content .extra {
  width: 40vw;
}
.wrapper .content .extra span {
  color: gray;
}
@media screen and (max-width: 768px) {
  .wrapper .content .extra {
    width: auto;
    margin-right: 20px;
  }
}
.wrapper .moreDetails {
  margin-top: 20px;
  color: white;
}
@media screen and (max-width: 768px) {
  .wrapper .moreDetails {
    margin: 20px;
  }
}
.wrapper .moreDetails p {
  color: white;
}
.wrapper .moreDetails span {
  color: gray;
}
.wrapper .similar {
  color: white;
}
@media screen and (max-width: 768px) {
  .wrapper .similar h2, .wrapper .similar p {
    margin: 0 20px;
  }
}
.wrapper .similar .media {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}
@media screen and (max-width: 768px) {
  .wrapper .similar .media {
    margin-left: 10px;
    gap: 1px;
  }
}

.loader {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}