.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.f_featureWrapper2 {
  width: 200px;
  background-color: #191919;
  overflow: hidden;
  text-overflow: ellipsis;
}
.f_featureWrapper2 .title {
  width: 200px;
}
@media screen and (max-width: 576px) {
  .f_featureWrapper2 {
    width: 145px;
  }
  .f_featureWrapper2 .title {
    width: 145px;
  }
}

.f_featureWrapper {
  background-color: #191919;
  position: relative;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  aspect-ratio: 1/1;
  height: 200px;
}
@media screen and (max-width: 576px) {
  .f_featureWrapper {
    height: 145px;
  }
}

.f_featureWrapperVideo3 {
  overflow: hidden;
  text-overflow: ellipsis;
  width: 275px;
}
@media only screen and (max-width: 768px) {
  .f_featureWrapperVideo3 {
    width: 346px;
  }
}

.f_featureWrapperVideo {
  background-color: #191919;
  position: relative;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  height: 180px;
}
@media only screen and (max-width: 768px) {
  .f_featureWrapperVideo {
    height: 150px;
  }
}

.f_featureWrapperMovie {
  background-color: #191919;
  aspect-ratio: 27/40;
  position: relative;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  height: 250px;
}

.content {
  opacity: 0;
  transition: all 0.3s ease-in;
}
@media only screen and (max-width: 676px) {
  .content {
    opacity: 1;
  }
}

.content.active {
  opacity: 1;
  transition: all 0.3s ease-out;
}

.f_featureContentWrapper {
  height: 40%;
  width: 100%;
  bottom: 0;
}

.f_featureActionBtn {
  height: 30px;
}

.f_featureSummary {
  color: #FFFFFF;
}

.f_featurePane {
  z-index: 9;
  padding: 10px;
}

.f_featurePlayBtn {
  background-color: transparent;
  border: none;
  position: absolute;
  right: 0;
  bottom: 5px;
}
.f_featurePlayBtn:focus {
  outline: 0;
}

.f_featureHeaderWrapper {
  position: absolute;
  width: auto;
  top: 0px;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
}

.f_featureHeaderWrapperTitle {
  background-color: rgba(0, 0, 0, 0.7);
  color: #FFFFFF;
  display: inline-block;
  font-size: 6px;
}

.f_defaultFeatureAvatar {
  height: 50px;
  width: 50px;
  margin-right: 10px;
}

.f_realFeatureAvater {
  height: 50px !important;
  width: 50px !important;
  border-radius: 50% !important;
}

.f_menuIcon {
  width: 15px;
}

.f_description {
  font-size: 12px;
  max-width: 170px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #aaa;
}
.f_description:hover {
  cursor: pointer;
}

.f_bottom_icon {
  height: 12px;
  color: white;
  margin-left: 10px;
  float: right;
}

.f_fontSize10 {
  font-size: 10px;
}

.f_fontSize12 {
  font-size: 12px;
}

.f_hoverCursor_icon {
  color: #2B7676;
}

.f_hoverCursor_icon:hover {
  cursor: pointer;
}

.f_fontSize16 {
  font-size: 16px;
  font-size: 1.1rem;
  line-height: 1.2rem;
  max-height: 2.4rem;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 0.5rem;
}

.f_hoverCursor {
  color: #2B7676;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 10px;
}

.f_hoverCursor:hover {
  cursor: pointer;
}

.views {
  font-size: 8px;
  color: white;
}

.viewallcomments {
  cursor: pointer;
}

.likeandcomment {
  display: flex;
  position: absolute;
  width: auto;
  bottom: 0px;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
  padding: 5px 5px;
  align-items: baseline;
}

.no_of_likes {
  margin-left: auto;
  font-size: 8px;
  color: white;
}

.title {
  font-size: 12px;
  padding: 0 2px;
  cursor: pointer;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-weight: bold;
}
.title::hover {
  color: red;
}

.rowBg {
  background-color: rgba(0, 0, 0, 0.75);
}