.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  margin-top: 60px;
}

.cover {
  position: relative;
}
.cover .movie {
  aspect-ratio: 27/40;
}
.cover .audio {
  aspect-ratio: 1/1;
}
.cover .video {
  aspect-ratio: 16/9;
}
.cover span {
  position: absolute;
  z-index: 1;
  bottom: 5px;
  right: 5px;
}
.cover img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.itemWrapper {
  position: relative;
}

.loader {
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  height: 100%;
  width: 100%;
}
.loader span {
  position: absolute;
  top: 50%;
  left: 50%;
}