.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  height: 240px;
  margin-bottom: 2rem;
  overflow: hidden;
}
@media screen and (max-width: 576px) {
  .wrapper {
    height: auto;
  }
}

.contentWrapper {
  position: relative;
  z-index: 9;
}

.coverWrapper {
  height: 200px;
  position: relative;
}

.playerWrapper {
  height: 40px;
  background-color: #8103FC;
}

.playerNameWrapper {
  color: #FFFFFF;
}

.playerAvatar {
  height: 40px;
  width: 40px;
  border-radius: 20px;
}

.title {
  font-weight: 700;
  font-size: 2rem;
  color: #FFFFFF;
  text-shadow: 2px 2px #0C1A22;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
@media screen and (max-width: 576px) {
  .title {
    font-size: 1.5rem;
    width: 250px;
  }
}

.artistName {
  margin-top: 1rem;
  color: #FFFFFF;
  text-shadow: 0.5px 0.5px #0C1A22;
  margin-bottom: 0.5rem;
  line-height: 0;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 9;
}

.controlWrapper {
  padding: 2rem;
}

.icon {
  height: 15px;
  filter: drop-shadow(0px 0px 1px rgba(0, 0, 0, 0.7));
}

.headerIcon {
  height: 20px;
}

.avatarImageWrapper {
  background-color: #8103FC;
  padding: 10px;
  border-radius: 50%;
}