.wrapper {
  background-color: rgba(0, 0, 0, 0.9);
  height: 100%;
}

.card {
  width: 90vw;
  height: 90vh;
  overflow-y: scroll;
  border-radius: 10px;
  padding: 20px;
  border: solid 1px rgba(255, 255, 255, 0.5);
  background-color: rgba(0, 0, 0, 0.5);
}
@media (min-width: 576px) {
  .card {
    width: 40vw;
    height: 70vh;
  }
}

.avatarInputWrapper {
  height: 150px;
}

.bannerWrapper {
  height: 150px;
}