.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.dragDropWrapper {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 10px;
  padding: 20px;
  height: 160px;
  width: 100%;
  color: #000000;
  padding: 5px;
}
.dragDropWrapper h3 {
  color: white;
  font-size: 24px;
  margin-bottom: 20px;
}

.dragActive {
  background-color: #F5F7FB;
  border: 1px dashed #727C7C;
}

.dragDropInner {
  width: 100%;
  height: 100%;
}

.dragTitle {
  font-size: 24px;
  font-family: Roboto;
  color: #727C7C;
  font-weight: bold;
}

.btnWrapper {
  background-color: #FC0135;
  padding: 2rem;
  height: 40px;
  border-radius: 42px;
  color: #FFFFFF;
  display: flex;
  align-items: center;
}
.btnWrapper .mobile {
  display: none;
}
@media (max-width: 576px) {
  .btnWrapper span {
    display: none;
  }
  .btnWrapper .mobile {
    display: block;
  }
}

.fileBtnWrapper {
  background: rgba(20, 20, 20, 0.22);
  border-radius: 50%;
  margin: 0 5px;
  height: 30px;
  padding: 8px;
}