.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.tabMenu {
  color: white;
  font-family: Roboto;
  font-size: 14px;
  background-color: transparent;
  border: none;
  padding: 0 1rem;
  margin-top: 1rem;
  border-radius: 3px;
  text-transform: uppercase;
}
.tabMenu:focus {
  outline: none;
}

.tabMenuActive {
  color: white;
  background-color: #8203FC;
}

.activeTabMenu {
  background-color: #FC0135;
  height: 5px;
  border-radius: 5px;
}

.mobile {
  width: 100%;
  max-width: 360px;
}