.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  position: relative;
  width: 150px;
  cursor: pointer;
  transition: all 0.3s ease-out;
}
.wrapper .cover {
  width: 150px;
  aspect-ratio: 27/40;
  object-fit: cover;
  object-position: center;
}
.wrapper .btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 45px;
  width: 45px;
}

.wrapper:hover {
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transform: scale(1.2);
  z-index: 5;
  border-radius: 5px;
}