.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  position: relative;
  min-height: 100%;
  padding: 1rem 2rem 10rem 2rem;
}

.heroContainer {
  position: relative;
  height: 300px;
}

.coverWrapper {
  position: absolute;
  background-color: #727C7C;
  height: 100%;
  width: 100%;
}

.avatarWrapper {
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid #8C8C8C;
  height: 200px;
  width: 200px;
  margin-right: 1rem;
}

.title {
  font-size: 3rem;
  font-weight: bold;
  font-family: Poppins;
}

.avatarContainer {
  position: relative;
  margin-bottom: 2rem;
  padding: 2rem;
}