.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  display: inline-block;
  position: relative;
  width: 100%;
  margin-right: 10px;
  margin-bottom: 10px;
}

.dummy {
  margin-top: 100%;
}

.featureWrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #514E4E;
  box-shadow: 3px 3px 3px #111;
}

.featureWrapper:before {
  content: " ";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border: 1px solid #333;
}

.featureContentWrapper {
  height: 40%;
  width: 100%;
  bottom: 0;
}

.featureActionBtn {
  height: 30px;
}

.featureSummary {
  color: #FFFFFF;
  margin: 5px;
}

.featurePane {
  position: absolute;
  z-index: 9;
  bottom: 0;
  align-items: flex-end;
  padding: 0.5rem 1rem;
}

.featurePlayBtn {
  background-color: transparent;
  border: none;
}
.featurePlayBtn:focus {
  outline: 0;
}

.featureHeaderWrapper {
  position: absolute;
  width: 100%;
}

.featureHeaderWrapperTitle {
  background-color: #0C1A22;
  color: #FFFFFF;
}

.defaultFeatureAvatar {
  height: 50px;
  width: 50px;
  margin-right: 10px;
}

.realFeatureAvater {
  height: 50px !important;
  width: 50px !important;
  border-radius: 50% !important;
}

.menuIcon {
  width: 15px;
}

.description {
  font-size: 12px;
  max-width: 170px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #aaa;
}

.bottom_icon {
  width: 25px;
  color: white;
  margin-left: 10px;
  float: right;
}

.fontSize10 {
  font-size: 10px;
}

.fontSize12 {
  font-size: 12px;
}

.fontSize16 {
  font-size: 16px;
}

.hoverCursor:hover {
  cursor: pointer;
}