.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.text {
  font-size: 14px;
  color: #FFFFFF;
}

.date {
  font-size: 10px;
  color: #727C7C;
}

.row {
  padding: 1em;
  background: rgba(0, 0, 0, 0.5);
  margin: 1rem 0;
  color: white;
}

.deleteStyle {
  margin-left: auto;
}

.deleteStyle:hover {
  color: red;
  cursor: pointer;
}

.replyButton:hover {
  cursor: pointer;
  color: #FC0135;
}

.replyButton.active {
  color: #FC0135;
}

.textArea {
  padding: 1em;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  color: rgba(255, 255, 255, 0.7);
}

.textArea::placeholder {
  color: rgba(255, 255, 255, 0.2);
}

.buttonAccent {
  background: rgba(252, 1, 53, 0.9);
  color: rgba(255, 255, 255, 0.9);
}

.buttonAccent:hover {
  background: #fc0135;
  color: white;
}