.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.progressBar {
  position: relative;
  background-color: #8C8C8C;
  width: 100%;
  height: 5px;
  float: left;
  overflow: visible;
  margin-top: -5px;
  margin-left: 5px;
  border-radius: 10px;
  padding: 0;
}

.knobMenu {
  position: absolute;
  text-align: center;
  width: 5px;
  height: 5px;
  top: 0px;
  z-index: 2;
  border-radius: 50%;
  background-color: transparent;
}

.rangeMenu {
  position: relative;
  background-color: #f1f1f1;
  height: 5px;
  border-radius: 5px;
  width: 0;
}