.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  display: flex;
  position: relative;
  align-items: stretch;
  width: 100%;
  box-sizing: border-box;
  overflow-x: auto;
}

.collapsed {
  height: 40px !important;
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 100%;
}

.no_media {
  font-size: 25px;
  text-align: center;
  color: #777 !important;
}

.wrapper::-webkit-scrollbar {
  width: 20px;
}

.wrapper::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

.wrapper::-webkit-scrollbar-thumb {
  background: #333;
  border: 1px solid black;
  border-radius: 20px;
}

.wrapper::-webkit-scrollbar-corner {
  background: transparent !important;
}

.titleWrapper {
  flex: 1;
}

.heading {
  font-size: 2.5rem;
  color: #FFFFFF;
  font-family: "Montserrat";
}
@media screen and (max-width: 576px) {
  .heading {
    font-size: 1.5rem;
  }
}