.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.background {
  background-color: black;
  height: 100vh;
  overflow: scroll;
}

.login-modal-top {
  padding-top: 0;
}

.foreContent {
  z-index: 2;
  padding-top: 0;
}

.f25 {
  font-size: 25px;
}

@media screen and (min-width: 576px) {
  .c-text-center {
    text-align: center;
  }
}
@media screen and (max-width: 576px) {
  .c-text-center {
    text-align: right;
  }
}

.btn-login {
  background-color: red;
  border: 1px solid red;
  box-shadow: 1px 1px 2px;
  border-radius: 4px;
  color: white;
  padding: 4px;
  min-width: 60px;
}

.btn-register {
  background-color: white;
  border: 1px solid #C18294;
  box-shadow: 1px 1px 2px;
  border-radius: 4px;
  color: #444444;
  padding: 4px;
  margin-left: 15px;
}

.btn-forget {
  margin-left: auto;
  border: none;
  background-color: transparent;
}

.w20 {
  width: 20px !important;
  height: 20px !important;
}

.goHome {
  position: absolute;
  top: 30px;
  left: 20px;
  z-index: 3;
  color: white;
  background: transparent;
  border: none;
}

.z3 {
  z-index: 3;
}
@media screen and (max-width: 576px) {
  .z3 {
    margin-bottom: 100px;
  }
}

@media screen and (min-width: 576px) {
  .login_logo_icon {
    text-align: center;
    width: 130px;
    height: 140px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
@media screen and (max-width: 576px) {
  .login_logo_icon {
    text-align: right;
    width: 80px;
    height: 90px;
    margin-right: 15px;
  }
}

.label {
  color: black;
  font-weight: bold;
}

.display-none {
  position: absolute;
  bottom: 0;
}
@media screen and (max-width: 576px) {
  .display-none {
    display: none;
  }
}

#login-mobile-top-shape {
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  height: 20%;
  border-radius: 0 0 90% 0;
  background: linear-gradient(#8103FC, #FC0135);
}