.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.header {
  background: black;
  display: flex;
}
.header .menu {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .menu a {
  all: unset;
  padding: 0 16px;
  white-space: nowrap;
  cursor: pointer;
}
.header .menu a:hover {
  color: white;
}
@media screen and (max-width: 768px) {
  .header .menu a {
    display: none;
  }
}

.section {
  background: black;
}

.faqIcon {
  width: 10px;
  transform: rotate(45deg);
}