.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  z-index: 9998;
}

.title {
  font-family: Montserrat;
  font-weight: bold;
  font-size: 18px;
  color: #FC0135;
}

.menuMobile {
  position: absolute;
  top: 70px;
  left: 0;
  height: 0;
  width: 100%;
  z-index: 999;
  background-color: #FFFFFF;
  overflow: hidden;
  padding: 0 1rem;
  right: 0;
  width: 30%;
  margin-left: auto;
  margin-right: 10px;
  transition: transform 0.3s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.3s cubic-bezier(0.77, 0.2, 0.05, 1), height 0.5s ease;
}
@media screen and (max-width: 768px) {
  .menuMobile {
    min-width: 119px;
    background-color: #818181;
    border-radius: 20px;
  }
}

.menuActive {
  height: 200px;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
@media screen and (max-width: 768px) {
  .menuActive {
    height: 108px;
    min-width: 119px;
    background-color: #818181;
    border-radius: 20px;
  }
}