.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.itemWrapper {
  position: relative;
}
.itemWrapper img.cover {
  height: 225px;
  width: 150px;
  object-fit: cover;
}
.itemWrapper div.deleteBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: red;
  border-radius: 50%;
  display: flex;
  height: 20px;
  width: 20px;
  justify-content: center;
  align-items: center;
}
.itemWrapper div.deleteBtn img {
  height: 13px;
  width: 13px;
}
.itemWrapper div.details {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  transition: all 0.3s ease-in;
  cursor: pointer;
}
.itemWrapper div.details p {
  margin: 0;
  padding: 5px;
  color: #F4F4F4;
}
.itemWrapper div:hover {
  background-color: rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease-out;
}

.tileCard {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 10px;
  padding: 20px;
}
.tileCard h3 {
  color: white;
  font-size: 24px;
  margin-bottom: 20px;
}
.tileCard .header {
  display: flex;
}
.tileCard .header .saveButton {
  margin-left: auto;
}
.tileCard .header .progress {
  margin-left: 10px;
  height: 30px;
  width: 30px;
}
.tileCard .header .iconButton {
  margin-left: 10px;
}
.tileCard .header .iconButton img {
  height: 30px;
  width: 30px;
}
@media (max-width: 992px) {
  .tileCard .header h4 {
    font-size: 13px;
  }
}
.tileCard .body {
  height: 0;
  overflow-y: scroll;
  transition: all 0.3s ease-in;
}
.tileCard .body.active {
  height: 330px;
  transition: all 0.3s ease-out;
}
@media (max-width: 992px) {
  .tileCard .body.active {
    height: 400px;
  }
}

.tileCard.uploadComplete {
  border: 1px solid #8103FC;
}