.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

body {
  background-color: rgba(0, 0, 0, 0.75);
}

.userAvatar {
  height: 50px;
  width: 50px;
  object-fit: cover;
  border-radius: 50%;
}

.dropdown {
  position: relative;
  cursor: pointer;
}
.dropdown span:hover {
  color: #FC0135;
}
.dropdown > div {
  position: absolute;
  right: 0;
  padding: 5px 10px;
  background: rgba(255, 255, 255, 0.1);
}
.dropdown > div strong {
  font-size: 12px;
  white-space: nowrap;
  margin: 0;
  font-weight: 300;
  cursor: pointer;
}
.dropdown > div strong:hover {
  color: #FC0135;
}