.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.container {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.wrapper {
  width: 100%;
  overflow: hidden;
  transition: all ease-in-out 0.5s !important;
}

.item {
  position: relative;
  transition: all ease-in 0.5s;
}

.active {
  position: relative;
  left: 0;
  flex: 1;
  min-width: max-content;
}

.next {
  left: 100vw;
  width: 0;
}

.previous {
  left: -100vw;
  width: 0;
}

.prevButton {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 0;
  top: 0;
  height: 100%;
  padding: 0 2.5rem;
  background: white;
  background: linear-gradient(270deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
}

.nextButton {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  top: 0;
  height: 100%;
  padding: 0 2.5rem;
  background: white;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
}

.indicators {
  position: absolute;
  bottom: 0;
  display: flex;
  width: 100%;
  justify-content: center;
  z-index: 2;
  padding: 10px 0;
}
.indicators div {
  border-radius: 20px;
  padding: 0.5rem;
  background-color: rgba(0, 0, 0, 0.3);
  margin-right: 5px;
}
.indicators div:hover {
  background-color: rgba(0, 0, 0, 0.8);
  cursor: pointer;
}
.indicators .solid {
  background-color: #fc0135;
}

.loader {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 10;
  height: 100%;
  width: 100%;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.brand {
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  z-index: 20;
  bottom: 10px;
  left: 10px;
  padding: 10px;
  border-radius: 10px;
}