.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.background {
  background-color: black;
  height: 100vh;
  overflow: scroll;
}

.login-modal-top {
  padding-top: 4rem;
}

.foreContent {
  z-index: 2;
  padding-top: 50px;
}

.f25 {
  font-size: 25px;
}

.btn-login {
  background-color: red;
  border: 1px solid red;
  box-shadow: 1px 1px 2px;
  border-radius: 4px;
  color: white;
  padding: 4px;
  margin-right: 5px;
}

.btn-register {
  width: 70px;
  background-color: white;
  border: 1px solid #C18294;
  box-shadow: 1px 1px 2px;
  border-radius: 4px;
  color: #444444;
  padding: 4px;
  margin-left: 10px;
}

.btn-forget {
  margin-left: auto;
  border: none;
  background-color: transparent;
}

.w20 {
  width: 20px !important;
  height: 20px !important;
}

.w40 {
  width: 40px !important;
}

.goHome {
  position: absolute;
  top: 30px;
  left: 20px;
  z-index: 3;
  color: white;
  background: transparent;
  border: none;
}

.signup-user-type {
  display: flex;
}
@media screen and (max-width: 576px) {
  .signup-user-type {
    justify-content: space-evenly;
  }
}

.z3 {
  z-index: 3;
}

.logo_icon {
  text-align: center;
  width: 130px;
  height: 140px;
  margin-top: 150px;
}

.label {
  color: black;
  font-weight: bold;
}

.group-item {
  width: 100%;
  min-width: 80px;
  height: 150px;
  background-color: #EAEAEA;
  box-shadow: 1px 1px 2px grey;
  margin-right: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: none;
}
@media screen and (max-width: 576px) {
  .group-item {
    height: 100px;
  }
}

.item-active {
  background-color: #8103FC;
  color: white;
}

.group-item:focus,
.group-item:focus-visible {
  outline: none;
}

.gotoLogin {
  margin-left: auto;
  border: none;
  background-color: transparent;
}

.display-none {
  position: absolute;
  bottom: 0;
}
@media screen and (max-width: 576px) {
  .display-none {
    display: none;
  }
}