.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

html,
body,
p {
  font-family: Poppins;
  font-size: 14px;
  color: #727C7C;
}

html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.panel-container {
  min-height: 100vh;
}

.content {
  display: inline-block;
  width: calc(100% - 220px);
  background-color: #323232;
  overflow-x: hidden;
  transition: all 0.5s linear;
  height: 100vh;
}
@media only screen and (max-width: 768px) {
  .content {
    width: 100%;
  }
}

.sideMenuWrapper {
  display: inline-block;
  width: 220px;
  transition: all 0.5s linear;
  height: 100vh;
}
@media only screen and (max-width: 768px) {
  .sideMenuWrapper {
    display: none;
  }
}

.hideSideMenu {
  width: 0;
  transition: all 0.5s linear;
}

.expandContent {
  width: 100% !important;
  transition: all 0.5s linear;
}

.content::-webkit-scrollbar {
  width: 15px;
}

.content::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

.content::-webkit-scrollbar-thumb {
  background: #333;
  border: 1px solid black;
  border-radius: 20px;
}

.content::-webkit-scrollbar-thumb:hover {
  background: #666;
}

.content::-webkit-scrollbar-corner {
  background: transparent !important;
}

.sideMenuMobile {
  display: none;
}
@media only screen and (max-width: 768px) {
  .sideMenuMobile {
    display: block;
    position: absolute;
    top: 70px;
    height: 0;
    width: 100%;
    z-index: 10009;
    overflow: hidden;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), height 0.5s ease;
  }
}

.sideMenuActive {
  height: 100%;
}

.homeFooter {
  position: fixed;
  z-index: 9999;
  width: 100%;
  bottom: 0;
  transition: all 0.5s;
}

.hideFooter {
  bottom: -55px;
}

.homeFooter:hover {
  bottom: 0;
}