.previewWrapper {
  margin-right: 2rem;
  margin-bottom: 2rem;
}

.temp {
  position: relative;
  width: 300px !important;
  min-width: 300px;
  min-height: 300px;
  max-height: 310px;
  float: left;
  margin-right: 1.5rem;
  box-shadow: 3px 3px 3px #111;
  background-color: #f5f5f5;
  color: #111;
  margin-left: 1rem;
  margin-right: 0;
}

.temp p {
  color: #111;
}

.defaultFeatureAvatar {
  height: 35px;
  width: 35px;
  margin-right: 10px;
}

.bottom_icon {
  width: 25px;
  color: white;
  margin-left: 10px;
  float: right;
}

.bottom_icon:hover {
  cursor: pointer;
}

.realFeatureAvater {
  height: 35px !important;
  width: 35px !important;
  border-radius: 50% !important;
}

.previewBkgWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 100%;
  overflow: hidden;
  margin-bottom: 10px;
}

.previewActionBtn {
  background-color: transparent;
  position: absolute;
  z-index: 99;
  border: none;
}
.previewActionBtn:focus {
  outline: none;
}

.previewActionIcon {
  height: 50px;
  width: 50px;
}

.title {
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
}

.description {
  font-size: 11px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* number of lines to show */
  -webkit-box-orient: vertical;
  margin-bottom: 5px;
}

.header {
  position: absolute;
  width: 100%;
  top: 0;
  display: flex;
  justify-content: flex-end;
}

.font12 {
  font-size: 12px;
}

.font10 {
  font-size: 10px;
}