.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  position: relative;
  overflow: hidden;
  height: 100vh;
}

.heading {
  font-size: 5rem;
  color: #34353D;
  font-weight: bold;
}

.content {
  color: #34353D;
  margin-top: 3rem;
  margin-bottom: 5rem;
}

.subheading {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.panel {
  position: relative;
  top: 8rem;
}

.clouds {
  position: absolute;
  z-index: -1;
}