.heading-1 {
  font-weight: 500;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.heading-2 {
  font-weight: 700;
  font-family: Montserrat;
  font-size: 24px;
  color: #FC0135;
}

.btn-primary {
  background: rgba(129, 3, 252, 0.9);
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-primary:hover {
  background: #8103FC;
  border-color: #8103FC;
}

.btn-outline-primary {
  color: #8103FC;
  border-color: rgba(129, 3, 252, 0.9);
}

.btn-outline-primary:hover {
  background: #8103FC;
  border-color: #8103fc;
}

.logo {
  background: -webkit-linear-gradient(45deg, #8103FC, #FC0135);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
}

.wrapper {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  transition: all 0.3s ease-out;
  overflow: hidden;
  min-width: 162px;
}

.wrapper:hover {
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transform: scale(1.2);
  z-index: 5;
  border-radius: 5px;
}

.backgroundWrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.backgroundWrapper .header {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
}

.title {
  font-weight: 700;
  margin: 0;
  padding: 0;
  line-height: 1em;
  margin-top: 0.5em;
  margin-bottom: 0.1em;
}

.description {
  font-size: 12px;
  font-family: Montserrat;
}